export const Actions = {
    SET_TABS: "SET_TABS",
    ADD_NOTE: "ADD_NOTE",
    REMOVE_LAST_NOTE: "REMOVE_LAST_NOTE",
    CHANGE_SCREEN: "CHANGE_SCREEN",
    EDIT_TABS_ADD: "EDIT_TABS_ADD",
    EDIT_TABS_DELETE: "EDIT_TABS_DELETE",
    ADD_ALERT: "ADD_ALERT",
    REMOVE_ALERT: "REMOVE_ALERT"
};
  
export const AddNote = (string, note)=> ({
    type: Actions.ADD_NOTE,
    payload: {
        string: string,
        note: note
    }
});

export const RemoveLastNote = () => ({
    type: Actions.REMOVE_LAST_NOTE
})

export const SetTabs = (tabs) => ({
    type: Actions.SET_TABS,
    payload: tabs
})

export const ChangeScreenAction = (screen, index) => ({
    type: Actions.CHANGE_SCREEN,
    payload: {
        name: screen.name,
        prev: screen.prev,
        next: screen.next,
        tabIndex: index
    }
})

export const EditTabsAdd = (string, note)=> ({
    type: Actions.EDIT_TABS_ADD,
    payload: {
        string: string,
        note: note
    }
});

export const EditTabsDelete = () => ({
    type: Actions.EDIT_TABS_DELETE
})

export const AddAlert = (variant, message) => ({
    type: Actions.ADD_ALERT,
    payload: {
        variant,
        message
    }
})

export const RemoveAlert = () => ({
    type: Actions.REMOVE_ALERT
})