import React, { useState } from 'react'
import { Button, Col, Container, Form, Row } from 'react-bootstrap'
import { connect } from 'react-redux'
import AddNoteForm from './AddNoteForm'
import { AddAlert, ChangeScreenAction, RemoveAlert } from '../redux/Actions';
import { screens } from '../constants';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowLeft } from '@fortawesome/free-solid-svg-icons';
import Parse from 'parse/dist/parse.min.js';

const Tab = (props) => {

    const [tabName, setTabName] = useState("");

    const saveTab = async () => {
        let Tab = new Parse.Object('Tabs');
        Tab.set('name', tabName);
        Tab.set('data', JSON.stringify(props.data));
        try {
            await Tab.save();
            props.dispatch(AddAlert("success", "Tab saved successfully!"))
            setTimeout(function() {props.dispatch(RemoveAlert())}, 3000);
        } catch {
            props.dispatch(AddAlert("danger", "Couldn't save the tabs at the moment"))
        }
    }

    return (
        <div className='tab'>
            <Button className="back-btn" onClick={() => props.dispatch(ChangeScreenAction(screens[props.screen.prev]))}><FontAwesomeIcon icon={faArrowLeft}/></Button>
            <Container>
                <Row className='mt-5'>
                    {props.data.map((strings, index) => {
                        return (
                            <Col xs={6} sm={4} md={3} xl={2} className='p-2' style={{textAlign: 'center'}}>
                                <div className='card-1 p-2'>
                                    {strings.map((string, item) => {
                                        return (
                                            <div>{string.map((note, index) => <>-{note}-</>)}</div>
                                        )
                                    })}
                                </div>
                            </Col>
                        )
                    })}
                </Row>
                <AddNoteForm/>
                <Row style={{justifyContent: 'center'}} className='mt-3'>
                    <Col xs={6} md={3} lg={2} className="tab-name-input">
                        <Form.Control type="text" placeholder="Enter Name" value={tabName} onChange={e => setTabName(e.target.value)}/>
                    </Col>
                    <Col xs={6} md={3} lg={2} className="text-center">
                        <Button variant='dark' onClick={saveTab} style={{width: '100%'}}>Save Tabs</Button>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

const mapStateToProps = state => ({
    data: state.data,
    screen: state.screen
});

const mapDispatchToProps = dispatch => ({
    dispatch
});

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Tab);