import { Actions } from "./Actions";

const stringToIndex = {
    'e': 0,
    'B': 1,
    'G': 2,
    'D': 3,
    'A': 4,
    'E': 5,
}

const rootReducer = (state, action) => {
    switch (action.type) {
        case Actions.ADD_NOTE: {
            
            if(state.data.length===0 || (state.data[state.data.length-1])[0].length===5) {
                const stringsData = []
                state.strings.forEach((element, index) => {
                    if(index===stringToIndex[`${action.payload.string}`]) {
                        stringsData.push([action.payload.note])
                    }
                    else {
                        stringsData.push(['-'])
                    }
                });

                const newData = [
                    ...state.data,
                    stringsData
                ]

                return {
                    ...state,
                    data: newData
                }

            }
            else {
                const data = structuredClone(state.data);
                const stringsData = state.data[state.data.length-1];
                data.pop();

                state.strings.forEach((item, index) => {
                    if(index===stringToIndex[`${action.payload.string}`]) {
                        stringsData[index].push(action.payload.note)
                    }
                    else {
                        stringsData[index].push('-')
                    }
                })

                data.push(stringsData);

                return {
                    ...state,
                    data: data
                }
            }
        }

        case Actions.REMOVE_LAST_NOTE:
            const data = structuredClone(state.data);
            const stringsData = state.data[state.data.length-1];
            data.pop();

            state.strings.forEach((item, index) => {
                stringsData[index].pop();
            })

            if(stringsData[0].length !== 0) {
                data.push(stringsData);
            }

            return {
                ...state,
                data: data
            }

        case Actions.SET_TABS:
            return {
                ...state,
                tabs: action.payload
            }
        
        case Actions.CHANGE_SCREEN: 
            return {
                ...state,
                screen: action.payload
            }
        

        case Actions.EDIT_TABS_ADD: {
            
            const tabIndex = state.screen.tabIndex;
            const data = state.tabs[tabIndex].data;
            const dataLength = data.length;

            if(dataLength ===0 || (data[dataLength-1])[0].length===5) {
                const stringsData = []
                state.strings.forEach((element, index) => {
                    if(index===stringToIndex[`${action.payload.string}`]) {
                        stringsData.push([action.payload.note])
                    }
                    else {
                        stringsData.push(['-'])
                    }
                });

                const newData = [
                    ...data,
                    stringsData
                ]

                const tabs = structuredClone(state.tabs);
                tabs[tabIndex] = {
                    ...tabs[tabIndex],
                    data: newData
                }

                return {
                    ...state,
                    tabs: tabs
                }

            }
            else {
                const newData = structuredClone(data);
                const stringsData = data[dataLength-1];
                newData.pop();

                state.strings.forEach((item, index) => {
                    if(index===stringToIndex[`${action.payload.string}`]) {
                        stringsData[index].push(action.payload.note)
                    }
                    else {
                        stringsData[index].push('-')
                    }
                })

                newData.push(stringsData);

                const tabs = structuredClone(state.tabs);
                tabs[tabIndex] = {
                    ...tabs[tabIndex],
                    data: newData
                }

                return {
                    ...state,
                    tabs: tabs
                }
            }
        }

        case Actions.EDIT_TABS_DELETE:  {
            const tabIndex = state.screen.tabIndex
            const data = state.tabs[tabIndex].data
            const newData = structuredClone(data);
            const stringsData = data[data.length-1];
            newData.pop();

            state.strings.forEach((item, index) => {
                stringsData[index].pop();
            })

            if(stringsData[0].length !== 0) {
                newData.push(stringsData);
            }

            const tabs = structuredClone(state.tabs);
            tabs[tabIndex] = {
                ...tabs[tabIndex],
                data: newData
            }

            return {
                ...state,
                tabs: tabs
            }
        }

        case Actions.ADD_ALERT: {
            return {
                ...state,
                alert: {
                    variant: action.payload.variant,
                    message: action.payload.message
                }
            }
        }

        case Actions.REMOVE_ALERT: {
            return {
                ...state,
                alert: null
            }
        }

        default:
            return state;
    }
};

export default rootReducer;