import React, { useEffect } from 'react'
import Tab from './components/Tab'
import Home from './components/Home'
import { connect } from 'react-redux'
import { screens } from './constants'
import ShowTabs from './components/ShowTabs'
import Parse from 'parse/dist/parse.min.js';
import Alert from 'react-bootstrap/Alert';
import { RemoveAlert } from './redux/Actions'


const PARSE_APPLICATION_ID = 'KEM0WQBwDiFuRfxiBLVo5c3Ab4jYXOURjwwaA8dw';
const PARSE_HOST_URL = 'https://parseapi.back4app.com/';
const PARSE_JAVASCRIPT_KEY = 'vxwMLoCc3sdHhw6895xz2s5Z7siZllJbgfLrNLb8';
Parse.initialize(PARSE_APPLICATION_ID, PARSE_JAVASCRIPT_KEY);
Parse.serverURL = PARSE_HOST_URL;

const App = (props) => {
  useEffect(() => {
    if(window.matchMedia('(prefers-color-scheme: dark)').matches) {
      document.body.classList.add("dark-mode")
    }
    else {
      document.body.classList.add("light-mode")
    }

    window.matchMedia('(prefers-color-scheme: dark)')
      .addEventListener('change', event => {
        if(event.matches) {
          document.body.classList.add("dark-mode")
          document.body.classList.remove("light-mode")
        }
        else {
          document.body.classList.add("light-mode")
          document.body.classList.remove("dark-mode")
        }

      });
  }, []);

  return (
    <div className='app'>
      {props.alert && 
        <div className='p-2' style={{position: 'fixed', top: '0px', width: '100%'}}>
          <Alert key={props.alert.variant} variant={props.alert.variant} style={{textAlign: 'center'}} onClose={() => props.dispatch(RemoveAlert())} dismissible>
            {props.alert.message}
          </Alert>
        </div>
      }
      {props.screen.name === screens.HOME.name && <Home/>}
      {props.screen.name === screens.ADD_TAB.name && <Tab/>}
      {props.screen.name === screens.SHOW_TAB.name && <ShowTabs/>}
    </div>
  )
}

const mapStateToProps = state => ({
  screen: state.screen,
  alert: state.alert
});

const mapDispatchToProps = dispatch => ({
  dispatch
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);
