import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { ChangeScreenAction, SetTabs } from '../redux/Actions';
import { screens } from '../constants';
import { Button, Col, Container, Row } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { Roller } from 'react-awesome-spinners';
import Parse from 'parse/dist/parse.min.js';

const Home = (props) => {

    const dispatch = props.dispatch;

    useEffect(() => {
        async function fetchData() {
            const tabs = [];
            const query = new Parse.Query('Tabs');
            const entries = await query.find();
            entries.forEach(entry => {
                tabs.push({
                    id: entry.id,
                    name: entry.attributes.name,
                    data: JSON.parse(entry.attributes.data)
                })
            })
            dispatch(SetTabs(tabs))
        }
        fetchData();
    }, [dispatch])

    return (
        <div>
            <Container>
                <Row className='text-center mt-5'>
                    <h1>Tabs</h1>
                </Row>
                <Row className="text-center" style={{justifyContent: 'center'}}>
                {!props.tabs && <div className='text-center mt-5'><Roller/></div>}
                {props.tabs && props.tabs.map((tab, index) => {
                    return (
                        <Col xs={12} sm={6} md={4} lg={3}>
                            <div 
                                className="card-1 p-4" 
                                onClick={() => dispatch(ChangeScreenAction(screens.SHOW_TAB, index))}
                                style={{
                                    marginTop: '20px',
                                    fontSize: '18px',
                                    fontWeight: 500
                                }}
                            >
                                {tab.name}
                            </div>
                        </Col>
                    )
                })}
                </Row>
            </Container>
            
            <Button className="new-tab-btn" onClick={() => dispatch(ChangeScreenAction(screens.ADD_TAB))}><FontAwesomeIcon icon={faPlus} size="2x"/></Button>        
        </div>
    )
}

const mapStateToProps = state => ({
    tabs: state.tabs
});

const mapDispatchToProps = dispatch => ({
    dispatch
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Home);