import React, { useState } from 'react'
import { Button, Col, Container, Form, Row } from 'react-bootstrap'
import { connect } from 'react-redux';
import { AddNote, RemoveLastNote } from '../redux/Actions';

const AddNoteForm = (props) => {

    const [string, setString] = useState((props.strings.length!==0)? `${props.strings[0]}` : '');
    const [note, setNote] = useState('');

    return (
        <Container>
            <Row style={{justifyContent: 'center'}} className="my-2">
                <Col xs={12} md={6} lg={4} className="form-1 border-radius p-4">
                    <Row >
                        <Col xs={6}>
                            <Form.Select onChange={(e) => setString(e.target.value)}>
                                {props.strings.map((item, index) => {
                                    return (
                                        <option id={index} value={`${item}`}>{item}</option>
                                    )
                                })}
                            </Form.Select>
                        </Col>
                        <Col xs={6}>
                            <Form.Control type="number" placeholder="Enter Note" value={note} onChange={e => setNote(e.target.value)}/>
                        </Col>
                    </Row>
                    <Row className='mt-3'>
                        <Col xs={6}>
                            <Button onClick={() => props.dispatch(AddNote(string, note))} style={{width: '100%'}}>Add Note</Button>
                        </Col>
                        <Col xs={6}>
                            <Button variant='danger' onClick={() => props.dispatch(RemoveLastNote())} style={{width: '100%'}}>Delete</Button>
                        </Col>
                    </Row>
                </Col>
            </Row>
        </Container>
    )
}

const mapStateToProps = state => ({
    strings: state.strings
})

const mapDispatchToProps = dispatch => ({
    dispatch
});

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(AddNoteForm);