import { faArrowLeft, faPencil } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import React, { useState } from 'react'
import { Button, Col, Container, Form, Row } from 'react-bootstrap'
import { connect } from 'react-redux'
import { screens } from '../constants'
import { AddAlert, ChangeScreenAction, EditTabsAdd, EditTabsDelete, RemoveAlert } from '../redux/Actions'
import Parse from 'parse/dist/parse.min.js';

const ShowTabs = (props) => {

    const [isEdit, setIsEdit] = useState(false);
    const [string, setString] = useState((props.strings.length!==0)? `${props.strings[0]}` : '');
    const [note, setNote] = useState('');

    const onSave = async () => {
        let Tab = new Parse.Object('Tabs');
        Tab.set('objectId', props.tabs[props.tabIndex].id)
        Tab.set('name', props.tabs[props.tabIndex].name);
        Tab.set('data', JSON.stringify(props.tabs[props.tabIndex].data));
        try {
            await Tab.save();
            props.dispatch(AddAlert("success", "Tab saved successfully!"))
            setTimeout(function() {props.dispatch(RemoveAlert())}, 3000);
        } catch {
            props.dispatch(AddAlert("danger", "Couldn't save the tabs at the moment"))
        }
        setIsEdit(!isEdit)
    }

    return (
        <div>
            <Button className="back-btn" onClick={() => props.dispatch(ChangeScreenAction(screens[props.screen.prev]))}><FontAwesomeIcon icon={faArrowLeft}/></Button>
            <Container>
                <Row className="mt-4" style={{textAlign: 'center'}}><h2>{props.tabs[props.tabIndex].name}</h2></Row>
                <Row className='mt-2' style={{justifyContent: 'center'}}>
                    {(props.tabs[props.tabIndex].data).map((strings, index) => {
                        return (
                            <Col xs={6} sm={4} md={3} xl={2} className='p-2' style={{textAlign: 'center'}}>
                                <div className='card-1 p-2'>
                                    {strings.map((string, item) => {
                                        return (
                                            <div>{string.map((note, index) => <>-{note}-</>)}</div>
                                        )
                                    })}
                                </div>
                            </Col>
                        )
                    })}
                </Row>
                {isEdit && 
                <>
                <Row style={{justifyContent: 'center'}} className="my-2 mx-2">
                    <Col xs={12} md={6} lg={4} className="form-1 border-radius p-4">
                        <Row >
                            <Col xs={6}>
                                <Form.Select onChange={(e) => setString(e.target.value)}>
                                    {props.strings.map((item, index) => {
                                        return (
                                            <option id={index} value={`${item}`}>{item}</option>
                                        )
                                    })}
                                </Form.Select>
                            </Col>
                            <Col xs={6}>
                                <Form.Control type="text" placeholder="Enter Note" value={note} onChange={e => setNote(e.target.value)}/>
                            </Col>
                        </Row>
                        <Row className='mt-3'>
                            <Col xs={6}>
                                <Button style={{width: '100%'}} onClick={() => props.dispatch(EditTabsAdd(string, note))}>Add Note</Button>
                            </Col>
                            <Col xs={6}>
                                <Button variant='danger' style={{width: '100%'}} onClick={() => props.dispatch(EditTabsDelete())}>Delete</Button>
                            </Col>
                        </Row>
                    </Col>
                </Row>
                <Row style={{justifyContent: 'center'}} className='mt-3'>
                    <Col xs={6} md={3} lg={2} className="text-center">
                        <Button variant='dark' style={{width: '100%'}} onClick={onSave}>Save Tabs</Button>
                    </Col>
                </Row>
                </>}

            </Container>
            <Button className="new-tab-btn" onClick={() => setIsEdit(!isEdit)}><FontAwesomeIcon icon={faPencil} size="2x"/></Button> 
        </div>
    )
}

const mapStateToProps = state => ({
    screen: state.screen,
    tabIndex: state.screen.tabIndex,
    strings: state.strings,
    tabs: state.tabs
});

const mapDispatchToProps = dispatch => ({
    dispatch
});

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(ShowTabs);